<template>
  <div id="pie" :style="totalsize">
    <div class="dragRectTop"></div>
    <div ref="echarts" :style="size">
      <!-- <e-charts :size="size" :options="options" /> -->
    </div>
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";

let length = 4
let dataA = []
let dataB = []
// generate random data,ranging from 6 to 16 ,most are around 10
// the second data is less than the first one by 1.1 to 1.2
function generateData() {
  let res = []
  let num = 0
  if (Math.random() >  0.7){
    num = Math.random() * 10 + 6
  }else{
    num = Math.random() * 2 + 8
  }
  num = num.toFixed(2)
  res.push(num)
  res.push((num * (Math.random() * 0.1 + 0.8)).toFixed(2))
  return res
}




export default {
  props: ["group_working_time"],
  watch: {
    group_working_time:function(){
      this.startFlag = true
      console.log("start")
    }

/*     group_working_time: function () {
      console.log("pie2");
      console.log(this.group_working_time);
      this.options.series[0].data = this.group_working_time["grouped_working_time"];
      this.options.series[1].data = this.group_working_time["non_grouped_working_time"];
      this.myChart.setOption(this.options);
    }, */
  },
  mounted() {
    this.myChart = echarts.init(this.$refs.echarts, null, {
      renderer: "canvas",
      useDirtyRect: false,
    });

    this.myChart.clear()

    dataA = []
    dataB = []

    for (let i = 0; i < length; i++) {
      let res = generateData()
      let mydate = new Date()
      let year = mydate.getFullYear();
      let month = mydate.getMonth() + 1;
      let day = mydate.getDate();
      let hours = mydate.getHours();
      let minutes = mydate.getMinutes();
      var seconds = mydate.getSeconds() - (length - i+1);
      let formattedTime = year + "-" + month + "-" + day + " " + hours + ":" + minutes+ ":"+seconds;

      dataA.push([formattedTime, res[0]])
      dataB.push([formattedTime, res[1]])
    }

    // this.options.series[0].data = dataA
    this.options.series[1].data = dataB

    this.myChart.setOption(this.options);
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });

    this.timer = setInterval ( () => {
    // do something
    let res = generateData()
      let mydate = new Date()
      let year = mydate.getFullYear();
      let month = mydate.getMonth() + 1;
      let day = mydate.getDate();
      let hours = mydate.getHours();
      let minutes = mydate.getMinutes();
      var seconds = mydate.getSeconds();
      let formattedTime = year + "-" + month + "-" + day + " " + hours + ":" + minutes+ ":"+seconds;

  console.log("dataA:")
  console.log(dataA)
  dataA.shift()
  dataA.push([formattedTime, res[0]])

  console.log(dataA)
  dataB.shift()
  dataB.push([formattedTime, res[1]])

  // console.log("Data:")
  // console.log(dataA)
  // console.log(dataB)

  if(this.startFlag == true){
    this.myChart.setOption({
      series: [
        {
          data: dataA,
        },
        {
          data: dataB,
        },
      ],
    });
}
else{
  this.myChart.setOption({
      series: [
        {
          // data: dataA,
        },
        {
          data: dataB,
        },
      ],
    });
}
    }, 3000);

  },
  data: function () {
    return {
      startFlag :false,
      dataA:[],
      dataB:[],
      mycharts: {},
      options: {
        title: {
          text: "组队/未组队工作时间",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 22,
          },
          left: "center",
          top: "5px",
        },
        legend:{
          show:true,
          data:["组队","未组队"],
          bottom:"0",

          textStyle:{
            color:"rgba(255,255,255,0.9)",
            fontSize: 18,
            fontStyle:'bold',
          }
        },
        /* grid: [
          {
            id: "grid_line",
            top: "20%",
          },
        ], */
        xAxis: {
          type: "time",
          boundaryGap: true,
          splitNumber: 4,
          axisLabel:{
            rotate : 30
          }
          // gridId: "grid_line",
        },
        yAxis: {
          type: "value",
          boundaryGap: true,
          // gridId: "grid_line",
        },
        series: [
          {
            // smooth: true,
            name:"组队",
            type: "line",
            data: [],
            itemStyle:{
              color: "#ffe434"
            },
            label: {
              show: true,
              fontSize: 20,
            },
            legend: {
              textStyle: 20,
            }
          },
          {
            name:"未组队",
            // smooth:true,
            type: "line",
            data: [],
            itemStyle:{
              color:"#56a3f1"
            },
            label: {
              show: true,
              fontSize: 20,
            },
            legend: {
              textStyle: 20,
            }
          },
        ],
      },
      size: {
        width: "100%",
        height: "4.8rem",
      },
      totalsize: {
        width: "100%",
        height: "5.5rem",
      },
    };
  },
};
</script>

<style scoped>
#pie {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.title {
  text-align: center;
  position: relative;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>
