<template>
  <div id="map" ref="echarts" :style="size">
    <!-- <e-charts :size="size" :options="options" /> -->
  </div>
</template>

<script>
import * as echarts from "echarts";
import * as china from "@/utils/china";

var curr_orders = [];

// 航线绘制
var driver_icon =
  "path://M869.9 499.3h-15L745.2 197.4H598.3v-66.7H398.1v66.7H251.3L141.1 500.3C97.5 506.6 64 544 64 589.3v99.4c0 49.8 40.3 90.1 90.1 90.1h8.4v36.6c0 43 34.8 77.8 77.8 77.8s77.8-34.8 77.8-77.8v-36.6h387.7v36.6c0 43 34.8 77.8 77.8 77.8s77.8-34.8 77.8-77.8v-36.6h8.4c49.8 0 90.1-40.3 90.1-90.1v-99.4c0.1-49.7-40.3-90-90-90zM164.2 678.8c-38.6 0-69.9-31.2-69.9-69.9 0-38.6 31.2-69.9 69.9-69.9s69.9 31.2 69.9 69.9-31.3 69.9-69.9 69.9z m140.9-214.9v24.2h-8v-29.8c1.4 0.4 2.9 0.5 4.4 0.5h4.2c-0.4 1.7-0.6 3.4-0.6 5.1zM441 488.2h-8V464c0-1.8-0.2-3.4-0.5-5.2h8.6v29.5h-0.1z m49.8 0v-71.6h-0.5c-2.1-24.5-22.6-43.7-47.5-43.7H295.2c-25.1 0-45.4 19.2-47.5 43.7h-0.5v71.6h-73c19.4-53.3 85.8-235.7 95.9-263.7h456c10.1 27.9 76.5 210.4 95.9 263.7H490.8z m369 190.6c-38.6 0-69.9-31.2-69.9-69.9 0-38.6 31.2-69.9 69.9-69.9 38.6 0 69.9 31.2 69.9 69.9 0.1 38.6-31.2 69.9-69.9 69.9zM369 251.5c31.1 0 56.4 25.3 56.4 56.4s-25.3 56.4-56.4 56.4-56.4-25.3-56.4-56.4 25.3-56.4 56.4-56.4z";
var convertData = function (data) {
  var res = [];
  for (var i = 0; i < data.length; i++) {
    var dataItem = data[i];
    var fromCoord = dataItem.coord[0]; //获取from经纬度
    var toCoord = dataItem.coord[1]; //获取to经纬度
    if (fromCoord && toCoord) {
      res.push({
        id: dataItem.id,
        coords: [fromCoord, toCoord], //经纬度
        effect: {
          period: Math.max(dataItem.full_value / 0.08, 3), //value 航班
        },
        lineStyle: {
          normal: {
            color: dataItem.color, //航线的颜色
          },
        },
      });
    }
  }
  return res;
};
var series = [];
series.push(
  {
    // 箭头,
    type: "lines",
    animation: false,
    zlevel: 2,
    symbol: ["none", "none"],
    symbolSize: 10,
    effect: {
      show: true,
      period: 10,
      trailLength: 0,
      symbolSize: 15,
      loop: false,
    },
    lineStyle: {
      normal: {
        color: "#FFF", //航线的颜色
        width: 2,
        opacity: 0.6,
        curveness: 0.2,
      },
    },
    data: convertData(curr_orders),
  },
  {
    // 司机位置,
    type: "effectScatter",
    coordinateSystem: "geo",
    zlevel: 2,
    data: curr_orders.map(function (dataItem) {
      return {
        name: String(dataItem.id),
        value: dataItem.coord[0],
      };
    }),
    rippleEffect: {
      brushType: "stroke",
    },
    label: {
      normal: {
        show: false,
        position: "right",
        formatter: "{b}",
      },
    },
    symbolSize: 2, // 图标大小
    itemStyle: {
      normal: {
        color: "#c23531",
      },
      emphasis: {
        areaColor: "#2B91B7",
      },
    },
  },
  {
    // 订单位置
    type: "effectScatter",
    coordinateSystem: "geo",
    zlevel: 2,
    data: curr_orders.map(function (dataItem) {
      return {
        name: String(dataItem.id),
        value: dataItem.coord[1],
      };
    }),
    rippleEffect: {
      brushType: "stroke",
    },
    label: {
      normal: {
        show: false,
        position: "right",
        formatter: "{b}",
      },
    },
    symbolSize: 8, // 图标大小
    itemStyle: {
      normal: {
        color: "#DA70D6",
      },
      emphasis: {
        areaColor: "#2B91B7",
      },
    },
  }
);

export default {
  data: function () {
    return {
      myChart: {},
      year: 2019,
      options: {
        geo: {
          show: false,
          map: "china",
          label: {
            emphasis: {
              show: true,
              color: "#fff",
            },
          },
          center: [116.36, 39.92],
          roam: false,
          // 放大我们的地图
          zoom: 12,
          itemStyle: {
            normal: {
              areaColor: "rgba(43, 196, 243, 0.42)",
              borderColor: "rgba(43, 196, 243, 1)",
              borderWidth: 1,
            },
            emphasis: {
              areaColor: "#2B91B7",
            },
          },
        },
        series: series,
      },
      size: {
        width: "59%",
        height: "11rem",
      },
    };
  },
  props: ["currentTime", "strategy"],
  watch: {
    // 监听options改变，重新生成图表，动态刷新
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.options);
    },
    currentTime: function () {
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        this.curr_orders = OrdersGreedy[this.currentTime]["orders"];
      } else {
        this.curr_orders = Orders[this.currentTime]["orders"];
      }
      this.options.series[0].data = convertData(this.curr_orders);
      this.options.series[1].data = this.curr_orders.map(function (dataItem) {
        return {
          name: String(dataItem.id),
          value: dataItem.coord[0],
        };
      });
      this.options.series[2].data = this.curr_orders.map(function (dataItem) {
        return {
          name: String(dataItem.id),
          value: dataItem.coord[1],
        };
      });
      this.options = { ...this.options };
    },
    strategy: function () {
      console.log("map capture strategy");
      console.log(this.strategy);
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        this.curr_orders = OrdersGreedy[this.currentTime]["orders"];
      } else {
        this.curr_orders = Orders[this.currentTime]["orders"];
      }
      this.options.series[0].data = convertData(this.curr_orders);
      this.options.series[1].data = this.curr_orders.map(function (dataItem) {
        return {
          name: String(dataItem.id),
          value: dataItem.coord[0],
        };
      });
      this.options.series[2].data = this.curr_orders.map(function (dataItem) {
        return {
          name: String(dataItem.id),
          value: dataItem.coord[1],
        };
      });
      this.options = { ...this.options };
    },
  },
  mounted() {
    // 首先获取div
    // var mainDiv = document.getElementById('main')
    // 基于准备好的dom，初始化echarts实例
    this.myChart = echarts.init(this.$refs.echarts);
    // 使用刚指定的配置项和数据显示图表。
    this.myChart.setOption(this.options);
    let that = this;
  },
};
</script>

<style scoped>
#map {
  /* background-image: url(../assets/line.png); */
  /* border: 0.5px solid rgba(255,255,255,0.2); */
  position: relative;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 0px;
}
a {
  color: #42b983;
}
</style>
