<template>
  <div id="group">
    <div class="charts">
      <div class="left">
        <pie :group_driver_pie_data="this.group_driver_pie_data"></pie>
        <pie4 :group_analytic_data="this.group_analytic_data"></pie4>
      </div>
      <div class="mid">
        <Map @mouseover_node="this.handleMouseoverNode" :group_graph="group_graph" :team_detail="team_detail"></Map>
      </div>
      <div class="right">
        <Ctrl
          @group_level_change="this.handleGroupLevelChange"
          :curr_info="curr_info"
        ></Ctrl>
        <pie2 :group_working_time="this.group_working_time"></pie2>
      </div>
    </div>
  </div>
</template>

<script>
var if_generate_group = true;
var if_save_group = false;

var group = {};

let analytic_data = {};
let driver_pie_data = {};
let working_time = {};

var group_ratio = 0.9;
var node_num = 100;
var grouped_node_num = Math.floor(node_num * group_ratio);
var raw_graph_edge_num = node_num * 2;
var group_num = 3;
var edge_num;
var group_level2group_num = [0, 2, 4, 6, 8];
// for generating working time data
let time = [1, 2, 3];
let ratio = 2;
let max_working_time = 10;

var max_symbolSize = 200;
var max_pos_range = [300, 300];
var outer_to_inner_ratio = 2;
var smallest_point_ratio = 0.05;

var belong_to_group = [];
var group_header_rad = [];
var nodes_in_group = [];
var group_header = [];
var symbolSize;
var nodes_num_in_group = [];
import ECharts from "@/components/ECharts";
import Map from "@/components/group/Map";
import Mapa from "@/components/group/Map-a";
import ProgressBar from "@/components/group/ProgressBar";
import Pie from "@/components/group/Pie";
import Pie4 from "@/components/group/Pie4";
import Pie2 from "@/components/group/Pie2";
import Pie3 from "@/components/group/Pie3";
import Ctrl from "@/components/group/Ctrl";
export default {
  name: "group",
  components: {
    ECharts,
    Map,
    Mapa,
    ProgressBar,
    Pie,
    Pie2,
    Pie3,
    Pie4,
    Ctrl,
  },
  data: function () {
    return {
      nowTime: "",
      flag: false,
      flagMap: false,
      flag2: false,
      currentTime: 0,
      tabName: "Tab1",
      group_level: 0,
      group_graph: {
        nodes: [],
        lines: [],
        categories: [],
      },
      team_detail: {},
      group_analytic_data: {},
      group_driver_pie_data: {},
      group_working_time: {},
      curr_info: {},
    };
  },
  methods: {
    handleGroupLevelChange(v) {
      this.group_level = v;
    },
    handleMouseoverNode(params) {
      console.log("mouseover params:");
      console.log(params);
      var cur_group_level = this.group_level;
      let group_id = params.data.category;
      var mouseover_data = {};


      // TODO: 
      let tmp = {
        "revenue": analytic_data[cur_group_level]["revenue"][group_id],
        "team_detail": analytic_data[cur_group_level]["team_detail"],
        "group_id": group_id,
      }
      this.group_analytic_data = tmp;


      console.log("aaa");
      console.log(this.group_analytic_data);
      mouseover_data["group_id"] = group_id;
      mouseover_data["header"] = analytic_data[cur_group_level]["headers"][group_id];
      mouseover_data["sum_of_total_revenue"] =
        analytic_data[cur_group_level]["sum_of_total_revenue"];
      mouseover_data["nodes_num"] = analytic_data[cur_group_level]["nodes_num"][group_id];
      this.curr_info = mouseover_data;
    },
    generate_group(node_num, group_num) {
      let group = {
        nodes: [],
        links: [],
        categories: [],
      };
      // generate id_list from 0 to node_num-1
      let id_list = [];
      for (let i = 0; i < node_num; i++) {
        id_list.push(i);
      }
      // group_size is a list, recording the num of nodes in each group
      // whose size is group_num
      let group_size = [];
      for (let i = 0; i < group_num; i++) {
        group_size.push(0);
      }

      // group_pos is a list, recording the position of each group
      // whose size is group_num
      let group_pos = [];
      for (let i = 0; i < group_num; i++) {
        group_pos.push([0, 0]);
      }

      // record each group of nodes
      belong_to_group = [];
      for (let i = 0; i < node_num; i++) {
        belong_to_group.push(-1);
      }

      // record group header radius
      group_header_rad = [];
      for (let i = 0; i < group_num; i++) {
        group_header_rad.push(0);
      }

      // record nodes in group
      nodes_in_group = [];
      for (let i = 0; i < group_num; i++) {
        nodes_in_group.push([]);
      }

      // record header of group
      group_header = [];
      for (let i = 0; i < group_num; i++) {
        group_header.push(-1);
      }

      // generate header nodes according to group_num
      // its "symbolsize" should be porportional to the num in the group
      for (let i = 0; i < group_num; i++) {
        // random select a node from id_list
        let index = Math.floor(Math.random() * id_list.length);
        let id = id_list[index];
        let group_id = i;
        belong_to_group[id] = group_id;
        id_list.splice(index, 1);
        // random select a group size ,but not larger than the rest of id_list
        group_size[group_id] =
          Math.floor(Math.random() * (id_list.length / group_num)) + 1;
        nodes_in_group[group_id].push(id);
        group_header[group_id] = id;
        // group radius
        group_header_rad[group_id] = (max_symbolSize * group_size[group_id]) / node_num;
        console.log(1111111);
        // random select a position for the group
        // each group should not overlap within the circle whose radius is group_header_rad[group_id]*outer_to_inner_ratio
        // check if the position is valid
        let x = 0;
        let y = 0;
        let valid = false;
        // x=Math.random()*max_pos_range[0]
        // y=Math.random()*max_pos_range[1]
        let count = 0;
        while (!valid) {
          x = Math.random() * max_pos_range[0];
          y = Math.random() * max_pos_range[1];
          console.log("generating pos for group_" + group_id + " x:" + x + " y:" + y);
          if (group_id == 0) {
            valid = true;
            break;
          }
          count++;
          if (count == 2 * group_num) {
            valid = true;
            break;
          }
          for (let j = 0; j < group_id; j++) {
            let dx = x - group_pos[j][0];
            let dy = y - group_pos[j][1];
            let dist = Math.sqrt(dx * dx + dy * dy);
            if (
              dist <
              (group_header_rad[group_id] + group_header_rad[j]) * outer_to_inner_ratio
            ) {
              valid = false;
              break;
            } else {
              valid = true;
            }
          }
        }
        group_pos[group_id] = [x, y];
        // choose a max symbolSize for the group
        if (group_header_rad[group_id] > max_symbolSize * smallest_point_ratio * 5) {
          symbolSize = group_header_rad[group_id];
        } else {
          symbolSize = max_symbolSize * smallest_point_ratio * 3;
        }
        // push the node into group
        group.nodes.push({
          id: id,
          name: "leader" + id,
          x: group_pos[group_id][0],
          y: group_pos[group_id][1],
          symbolSize: symbolSize,
          category: group_id,
        });
      }

      // generate the rest of nodes
      for (let i = 0; i < id_list.length; i++) {
        let id = id_list[i];
        let group_id = Math.floor(Math.random() * group_num);
        belong_to_group[id] = group_id;
        nodes_in_group[group_id].push(id);

        // generate a random position in the circle whose radius is group_header_rad[group_id]*outer_to_inner_ratio and center is group_pos[group_id]
        // but not overlap with the header node
        let rand = Math.random() * (outer_to_inner_ratio - 1) + 2;
        let x =
          group_pos[group_id][0] +
          rand * group_header_rad[group_id] * Math.cos(Math.random() * 2 * Math.PI);
        let y =
          group_pos[group_id][1] +
          rand * group_header_rad[group_id] * Math.sin(Math.random() * 2 * Math.PI);

        group.nodes.push({
          id: id,
          name: "driver" + id,
          x: x,
          y: y,
          symbolSize: max_symbolSize * smallest_point_ratio,
          category: group_id,
        });
      }

      // generate links
      // all nodes are connected to the header node of its group
      // ,nodes in the same group are connected to each other randomly
      // ,nodes in different group are connected to each other randomly with a probability of 0.1

      for (let i = 0; i < node_num; i++) {
        let group_id = belong_to_group[i];
        // connect to the header node of its group
        group.links.push({
          source: "" + i,
          target: "" + group_header[group_id],
        });
        // connect to other nodes in the same group
        for (let j = 0; j < group_size[group_id]; j++) {
          if (Math.random() > 0.9) {
            group.links.push({
              source: "" + i,
              target: "" + nodes_in_group[group_id][j],
            });
          }
        }
        // connect to other nodes in different group
        /*  for(let j=0;j<node_num;j++){
        if(j!=i){
            if(Math.random()>0.95){
                group.links.push({
                    "source":i,
                    "target":j
                })
            }
        }
    } */
      }


      console.log(nodes_in_group);
      console.log(group_header);
      console.log(group.links);
      // generate categories
      for (let i = 0; i < group_num; i++) {
        group.categories.push({
          name: "车队" + (i+1)
        });
      }

      // caculate nodes number in each group
      nodes_num_in_group = [];
      for (let i = 0; i < group_num; i++) {
        nodes_num_in_group.push(nodes_in_group[i].length);
      }
      let revenue_distribution = [];
      let total_revenue = [];
      let sum_of_total_revenue = 0;
      // generate revenue distribution for each group
      // approximately 10% of the nodes are high revenue nodes
      // approximately 20% of the nodes are low revenue nodes
      // the rest of the nodes are medium revenue nodes
      for (let i = 0; i < group_num; i++) {
        let d1 = Math.random();
        let d2 = Math.random() * (1 - d1);
        let high_revenue_num = Math.floor(nodes_num_in_group[i] * d1);
        let low_revenue_num = Math.floor(nodes_num_in_group[i] * d2);
        let medium_revenue_num =
          nodes_num_in_group[i] - high_revenue_num - low_revenue_num;
        revenue_distribution.push([
          low_revenue_num,
          medium_revenue_num,
          high_revenue_num,
        ]);
        total_revenue.push(
          low_revenue_num * 60 + medium_revenue_num * 500 + high_revenue_num * 1200
        );
        sum_of_total_revenue += total_revenue[i];
      }


      // total revenue change according to group_num
      if (group_num <= 30){
        sum_of_total_revenue = sum_of_total_revenue + Math.floor((Math.random() * 0.2 + 1) * group_num * node_num * 100);
      }
      else{
        sum_of_total_revenue = sum_of_total_revenue + Math.floor((Math.random() * 0.2 + 1) * (group_num - 30) * node_num * 100);
      }

      let analytic_data = {
        headers: group_header,
        nodes_num: nodes_num_in_group,
        total_revenue: total_revenue,
        revenue: revenue_distribution,
        sum_of_total_revenue: sum_of_total_revenue
      };

      // generate information for each group
      // header, teammate, age_range, sex_ratio, in_city
      let names = ["张**","高**","王**","白**","马**","胡**","李**","刘**","陈**","杨**","赵**","黄**","周**","吴**","郑**","徐**","孙**","朱**","许**","何**","吕**","施**","张**","高**","王**","白**","马**","胡**","李**","刘**","陈**","杨**","赵**","黄**","周**","吴**","郑**","徐**","孙**","朱**","许**","何**","吕**","施**"]
      let teammate_len = 4

      let header=[]
      let teammate=[]
      let age_range=[]
      let sex_ratio=[]
      let in_city=[]
      let team_detail ={

      }

      // generate header,teteamate,age_range,sex_ratio,in_city for each group
      for (let i=0;i<group_num;i++){
        header.push(names[Math.floor(Math.random()*names.length)])
        teammate.push([])
        for(let j=0;j<teammate_len;j++){
          teammate[i].push(names[Math.floor(Math.random()*names.length)])
        }
        // age range
        // should be a random number between 18 and 60
        // generate a lower bound and an upper bound
        let lower_bound = Math.floor(Math.random()*42)+18
        let upper_bound = Math.floor(Math.random()*(60-lower_bound))+lower_bound
        age_range.push([lower_bound,upper_bound])

        // sex ratio
        // should be a random float between 1.5 to 3
        sex_ratio.push(Math.random()*1.5+1.5)

        // in city
        // should be 0/1
        in_city.push(Math.floor(Math.random()*2))

      }

      // generate battling team
      // randomly generate pairs of teams, which are competing with each other
      // all team should be in a pair
      let battling_team = {}
      let team_num = group_num
      let team_list = []
      for(let i=0;i<team_num;i++){
        team_list.push(i)
      }
      while(team_list.length>0){
        let team1_index = Math.floor(Math.random()*team_list.length)
        let team1 = team_list[team1_index]
        team_list.splice(team1_index,1)
        let team2_index = Math.floor(Math.random()*team_list.length)
        let team2 = team_list[team2_index]
        team_list.splice(team2_index,1)
        battling_team[team1]=team2
        battling_team[team2]=team1
      }



      team_detail={
        header:header,
        teammate:teammate,
        age_range:age_range,
        sex_ratio:sex_ratio,
        in_city:in_city,
        battling_team:battling_team,
        total_revenue: total_revenue,
        nodes_num:nodes_num_in_group
      }

      // generate links for battling team
      // each  team header should link to its rival team header
      for(let i=0;i<group_num;i++){
        group.links.push({
          source:"" +group_header[i],
          target:""+group_header[battling_team[i]],

          lineStyle:{
            color:"#ff0000",
            width:4
          }
        })
      }

      // return the group
      return { group: group, analytic_data: analytic_data,team_detail:team_detail };
    },
    generate_random_graph(node_num, edge_num) {
      let graph = {
        nodes: [],
        links: [],
        categories: [],
      };
      // generate nodes
      for (let i = 0; i < node_num; i++) {
        graph.nodes.push({
          id: "" + i,
          name: "driver" + i,
          x: Math.random() * max_pos_range[0],
          y: Math.random() * max_pos_range[1],
          symbolSize: max_symbolSize * smallest_point_ratio,
          category: 0,
          itemStyle: {
            color: "#6fa0b6",
          },
        });
      }
      // generate links
      for (let i = 0; i < edge_num; i++) {
        let source = Math.floor(Math.random() * node_num);
        let target = Math.floor(Math.random() * node_num);
        while (source == target) {
          target = Math.floor(Math.random() * node_num);
        }
        graph.links.push({
          source: "" + source,
          target: "" + target,
          lineStyle: {
            color: "#8ecdea",
          },
        });
      }
      // generate categories
      graph.categories.push({
        name: "未组队",
        /* textStyle: {
          color: "white",
        }, */
        
      });

      let high_revenue_num = Math.floor(node_num * 0.05);
      let low_revenue_num = Math.floor(node_num * 0.1);
      let medium_revenue_num = node_num - high_revenue_num - low_revenue_num;
      let analytic_data = {
        headers: [-1],
        nodes_num: [node_num],
        total_revenue: [node_num * 200],
        sum_of_total_revenue: node_num * 200 + Math.floor((Math.random() * 0.2 + 1) * 4 * node_num * 100),
        revenue: [[low_revenue_num, medium_revenue_num, high_revenue_num]],
      };

      return { group: graph, analytic_data: analytic_data ,team_detail:{}};
    },
    generate_working_time(time, ratio, max_working_time) {
      let grouped_working_time = [];
      let non_grouped_working_time = [];
      for (let i = 0; i < time.length; i++) {
        grouped_working_time.push([time[i], time[i] * ratio]);
        non_grouped_working_time.push([time[i], time[i]]);
      }
      let res = {
        grouped_working_time: grouped_working_time,
        non_grouped_working_time: non_grouped_working_time,
      };
      return res;
    },
    generate_driver_pie_data(analytic_data, node_num, grouped_node_num) {
      if (analytic_data["headers"][0] == -1) {
        return [
          {
            name: "未组队",
            value: node_num,
            itemStyle: {
              color: "#ccc",
            },
          },
        ];
      }
      let driver_pie_data = analytic_data["nodes_num"].map(function (item, idx) {
        return {
          name: "车队" + (idx+1),
          value: item,
        };
      });
      driver_pie_data.push({
        name: "未组队",
        value: node_num - grouped_node_num,
        itemStyle: {
          color: "#ccc",
        },
      });
      return driver_pie_data;
    },
  },
  watch: {
    group_level: function () {
      let group_level = this.group_level;
      console.log(group_level);
      if (group[group_level] == undefined) {
        let tmp;
        if (group_level == 0) {
          tmp = this.generate_random_graph(node_num, raw_graph_edge_num);
        } else {
          tmp = this.generate_group(grouped_node_num, group_level);
        }
        group[group_level] = tmp["group"];
        analytic_data[group_level] = tmp["analytic_data"];
        analytic_data[group_level]["team_detail"] = tmp["team_detail"];
        driver_pie_data[group_level] = this.generate_driver_pie_data(
          analytic_data[group_level],
          node_num,
          grouped_node_num
        );
        working_time[group_level] = this.generate_working_time(
          time,
          ratio,
          max_working_time
        );
      }
      this.group_graph = group[group_level];
      this.team_detail = analytic_data[group_level]["team_detail"];
      let tmp ={
        "revenue": analytic_data[group_level]["revenue"][0],
        "team_detail": analytic_data[group_level]["team_detail"],
        "group_id": 0,
      }
      console.log("tmp");
      console.log(tmp);
      this.group_analytic_data = tmp;
/*       this.group_analytic_data["revenue"] = analytic_data[group_level]["revenue"][0]; // default get group 0
      this.group_analytic_data["team_detail"] = analytic_data[group_level]["team_detail"]; // default get group 0 */
      this.group_driver_pie_data = driver_pie_data[group_level];
      this.group_working_time = working_time[group_level];
      var mouseover_data = {};
      mouseover_data["group_id"] = 0;
      mouseover_data["header"] = analytic_data[group_level]["headers"][0];
      mouseover_data["sum_of_total_revenue"] = analytic_data[group_level]["sum_of_total_revenue"];
      mouseover_data["nodes_num"] = analytic_data[group_level]["nodes_num"][0];

      this.curr_info = mouseover_data;
    },
  },
  mounted() {
    let group_level = this.group_level;
    if (group[group_level] == undefined) {
      let tmp;
      if (group_level == 0) {
        tmp = this.generate_random_graph(node_num, raw_graph_edge_num);
      } else {
        tmp = this.generate_group(grouped_node_num, group_level2group_num[group_level]);
      }
      group[group_level] = tmp["group"];
      analytic_data[group_level] = tmp["analytic_data"];
      analytic_data[group_level]["team_detail"] = tmp["team_detail"];
      driver_pie_data[group_level] = this.generate_driver_pie_data(
        analytic_data[group_level],
        node_num,
        grouped_node_num
      );
      working_time[group_level] = this.generate_working_time(
        time,
        ratio,
        max_working_time
      );
    }
    this.group_graph = group[group_level];
    this.group_analytic_data["revenue"] = analytic_data[group_level]["revenue"][0]; // default get group 0
    this.group_analytic_data["team_detail"] = analytic_data[group_level]["team_detail"];
    this.group_analytic_data["group_id"] = 0;
    this.group_driver_pie_data = driver_pie_data[group_level];
    this.curr_info.sum_of_total_revenue = node_num * 200 + Math.floor(Math.random() * 1000)
    // this.group_working_time = working_time[group_level];
  },
};
</script>

<style>
.charts {
  flex: 0;
  width: 100vw;
  padding: 0.15rem;
  display: flex;
}

.charts .left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.charts .mid {
  flex: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.charts .right {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.mid .title {
  margin: 0.1rem 0.1rem 2rem 0.1rem;
  padding: 0.2rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.2);
}

.mid .title .number {
  width: 100%;
  height: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.7rem;
  color: #f5d0a9;
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
}

.borderTop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.1rem;
}

.borderTop::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0.4rem;
  height: 0.2rem;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
}

.borderbottom {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 0.1rem;
}
.borderbottom::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 0.4rem;
  height: 0.2rem;
  border-bottom: 2px solid #008c14;
  border-right: 2px solid #008c14;
}

.mid .title .number span:nth-of-type(1) {
  width: 50%;
  text-align: center;
}

.mid .title .number span:nth-of-type(3) {
  width: 50%;
  text-align: center;
}

.mid .title .number span:nth-of-type(2) {
  height: 0.6rem;
  line-height: 1.4rem;
  border-left: 1px solid rgba(255, 255, 255, 0.1);
}

.mid .title .desc {
  width: 100%;
  height: 0.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
