<template>
  <div id="pie" :style="size">
    <div class="dragRectTop"></div>
    <e-charts :size="size" :options="option" />
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import ECharts from "@/components/ECharts";
export default {
  components: {
    ECharts,
  },
  data: function () {
    return {
      flag: true,
      size: {
        width: "100%",
        height: "5.5rem",
      },
      option: {
        color: ["#56A3F1", "#FFE434", "#67F9D8"],
        title: {
          text: "Algorithm Performance Radar Chart",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 16,
          },
          left: "center",
          top: "5px",
        },
        legend: {
          textStyle: {
            color: "white",
            fontSize: 14,
          },
          top: "32px",
          data: ["Peak", "Average", "Lowest"],
        },
        radar: {
          shape: "circle",
          center: ["50%", "60%"],
          radius: 110,
          indicator: [
            { name: "Utility", max: 100 },
            { name: "Vehicles", max: 100 },
            { name: "Response Rate", max: 100 },
            { name: "Requests", max: 100 },
            { name: "Orders", max: 100 },
          ],
          nameGap: 5,
          axisName: {
            formatter: "{value}",
            color: "white",
            fontSize: 14,
          },
          splitArea: {
            areaStyle: {
              shadowColor: "rgba(0, 0, 0, 0.2)",
              shadowBlur: 10,
            },
          },
          axisLine: {
            lineStyle: {
              color: "rgba(211, 253, 250, 0.8)",
            },
          },
          splitLine: {
            lineStyle: {
              color: "white",
            },
          },
        },
        series: [
          {
            name: "Peak/Average/Lowest",
            type: "radar",
            data: [
              {
                value: [75, 89, 60, 79, 94],
                name: "Peak",
              },
              {
                value: [60, 78, 50, 60, 85],
                name: "Average",
              },
              {
                value: [38, 52, 30, 45, 75],
                name: "Lowest",
              },
            ],
          },
        ],
      },
    };
  },
  props: ["mapStyle", "currentTime"],
  watch: {
    // 监听options改变，重新生成图表，动态刷新
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.option);
    },
    currentTime: function () {
      this.option = { ...this.option };
    },
    // { name: "收入", max: 100 },
    // { name: "车辆", max: 100 },
    // { name: "应答率", max: 100 },
    // { name: "拒单率", max: 100 },
    // { name: "订单", max: 100 },
    mapStyle: function () {
      if (this.mapStyle == 1)
        this.option.series[0].data = [
          {
            value: [75, 89, 60, 69, 80],
            name: "Peak",
          },
          {
            value: [60, 78, 50, 60, 76],
            name: "Average",
          },
          {
            value: [38, 52, 30, 45, 75],
            name: "Lowest",
          },
        ];
      else if (this.mapStyle == 2)
        this.option.series[0].data = [
          {
            value: [95, 89, 74, 69, 80],
            name: "Peak",
          },
          {
            value: [80, 78, 68, 60, 76],
            name: "Average",
          },
          {
            value: [60, 52, 50, 54, 75],
            name: "Lowest",
          },
        ];
      else
        this.option.series[0].data = [
          {
            value: [100, 90, 90, 80, 92],
            name: "Peak",
          },
          {
            value: [80, 78, 85, 80, 85],
            name: "Average",
          },
          {
            value: [70, 62, 70, 80, 75],
            name: "Lowest",
          },
        ];
      this.options = { ...this.option };
    },
  },
  mounted() {
    // 首先获取div
    // var mainDiv = document.getElementById('main')
    // 基于准备好的dom，初始化echarts实例
    // this.myChart = echarts.init(this.$refs.echarts);
    // 使用刚指定的配置项和数据显示图表。
    // this.myChart.setOption(this.option);
    let that = this;
  },
};
</script>

<style scoped>
#pie {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>