<template>
  <div id="map" ref="echarts" :style="size">
    <!-- <e-charts :size="size" :options="options" /> -->
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  data: function () {
    return {
      myChart: {},

      options: {
        tooltip:{
        formatter:(params)=>{
          console.log("params:")
          console.log(params)
          if (params["data"]["category"] == undefined){
            return 
          }
          let group_id = params["data"]["category"]
          let header = this.team_detail["header"][group_id]
          let total_rev = this.team_detail["total_revenue"][group_id]
          let nodes_num = this.team_detail["nodes_num"][group_id]
          let res =  `<div style="color: black;font-size: 14px; padding:0 12px;line-height: 24px">
            <span style="display:inline-block;margin-right:5px;border-radius:2px;width:10px;height:10px;background-color:${[
                    params.color, // 默认是小圆点，我们将其修改成有圆角的正方形，这里用的是模板字符串。并拿到对应颜色、名字、数据
                  ]};"></span>
            队伍: ${ group_id + 1} <br> 
           队长: ${ header}  <br>
           总效用:  ${total_rev} <br>
           成员数量：${nodes_num}
          </div>`
          console.log(res)
          return  res
        },
        textStyle: {
          color: "black",
        },
      },

        title: {
          show: false,
          textStyle: {
            color: "white",
          },
          text: "Les Miserables",
          subtext: "Default layout",
          top: "bottom",
          left: "right",
        },
       /*  grid: [
          {
            id: "grid_line",
            // left: '0%',
            right: "10%",
            //top: '50%',
            bottom: "10%",
            height: "40%",
            width: "20%",
          },
        ], */
        legend:  {
            show:false,
            textStyle: {
              color: "white",
              fontSize: 18, 
            },
            data: [],
          },
        
       /*  xAxis: {
          type: "category",
          boundaryGap: true,
          gridId: "grid_line",
        },
        yAxis: {
          type: "value",
          boundaryGap: [0, "100%"],
          gridId: "grid_line",
        }, */
        animationDuration: 1500,
        animationEasingUpdate: "quinticInOut",
        series: [
          {
            // name: "Les Miserables",
            type: "graph",
            layout: "none",
            data: [],
            links: [],
            categories: [],
            roam: true,
            label: {
              
              position: "right",
              formatter: "{b}",
            },
            lineStyle: { curveness: 0.3 },
            emphasis: {
              focus: "adjacency",
              lineStyle: {
                width: 10,
              },
            },
          },
        ],
      },
      size: {
        width: "100%",
        height: "10rem",
      },
    };
  },
  methods: {},
  props: ["group_graph","team_detail"],
  watch: {
    // 监听options改变，重新生成图表，动态刷新
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.options);
    },
    group_graph: function () {
      this.options.legend = [
        {
          textStyle: {
            color: "white",
          },
          // selectedMode: 'single',
          data: this.group_graph.categories.map(function (a) {
            return a.name;
          }),
        },
      ];
      this.options.series[0].data = this.group_graph.nodes;
      this.options.series[0].links = this.group_graph.links;
      this.options.series[0].categories = this.group_graph.categories;
      console.log("len")
      console.log(Object.keys(this.group_graph.categories).length)
      if(Object.keys(this.group_graph.categories).length >= 30){
        this.options.legend[0].show = false;
        console.log("close")
      }
      else{
        this.options.legend[0].show = true;
        console.log("show")
      }
      console.log(this.options)
      this.myChart.setOption(this.options);
    },

    

  },


  mounted() {
    // 初始化数据
    console.log(this.options);
    this.options.legend = [
      {
        // selectedMode: 'single',
        textStyle:{
          fontSize:18,
        },
        data: this.group_graph.categories.map(function (a) {
          return a.name;
        }),
      },
    ];

    this.options.series[0].data = this.group_graph.nodes;
    this.options.series[0].links = this.group_graph.links;
    this.options.series[0].categories = this.group_graph.categories;

    this.myChart = echarts.init(this.$refs.echarts, null, {
      renderer: "canvas",
      useDirtyRect: false,
    });

    this.group_graph.nodes.forEach(function (node) {
      node.label = {
        show: node.symbolSize > 30,
      };
    });

    // 使用刚指定的配置项和数据显示图表。
    this.myChart.setOption(this.options);

    // 注册事件并传递给父组件
    let that=this;
    this.myChart.on("mouseover", function (params) {
      console.log("map detect mouse over");
      if (params.dataType === "node") {
        that.$emit("mouseover_node", params);
      }
    });

    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  },
};
</script>

<style scoped>
#map {
  /* background-image: url(../assets/line.png); */
  /* border: 0.5px solid rgba(255,255,255,0.2); */
  position: relative;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 0px;
}
a {
  color: #42b983;
}
</style>
