<template>
  <div id="bar">
    <div class="dragRectTop"></div>
    <h2 style="text-align: center">Demonstration Controller</h2>
    <template>
      <div class="block" style="margin: auto; margin-top: 5%; width: 80%">
        <div class="demonstration" style="font-size: 16px">
          Time: 
          {{ nowtime }}
        </div>

       <div>
          <el-slider
            v-model="currentTime"
            :max="96"
            :show-tooltip="false"
          ></el-slider>
        </div>
      </div>
    </template>
    <div style="text-align: center">
      <el-select
        v-model="strategy"
        placeholder="策略选择"
        style="
          width: 170px;
          height: 40px;
          margin-right: 10px;
          vertical-align: middle;
        "
      >
        <el-option
          key="0"
          label="Greedy Algorithm"
          value="0"
        >
        </el-option>

        <el-option
          key="1"
          label="Learning to Dispatch"
          value="1"
        >
        </el-option>
        <el-option
          key="2"
          label="Kuhn-Munkres Algorithm"
          value="2"
        >
        </el-option>
      </el-select>
      <el-button v-if="this.flag === true" type="success" @click="start"
        style="font-size: 25px; height: 40px; width: 40px; padding: 0; vertical-align: middle; display: inline-flex; align-items: center; justify-content: center;"
        icon="el-icon-video-play" circle>
      </el-button>
      <el-button v-if="this.flag === false" type="warning" @click="stop"
        style="font-size: 25px; height: 40px; width: 40px; padding: 0; vertical-align: middle; display: inline-flex; align-items: center; justify-content: center;"
        icon="el-icon-video-pause" circle>
      </el-button>
      <el-button type="primary" @click="reset"
        style="font-size: 25px; height: 40px; width: 40px; padding: 0; vertical-align: middle; display: inline-flex; align-items: center; justify-content: center;"
        icon="el-icon-refresh" circle>
      </el-button>
    </div>
    <br />
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import ECharts from "@/components/ECharts";

var Time = 0;
export default {
  components: {
    ECharts,
  },
  data: function () {
    return {
      currentTime: 20,
      updateTime: null,
      nowtime: "Loading……",
      flag: false,
      strategy: "0",
    };
  },
  methods: {
    start() {
      const Orders = require("@/static/json/Orders");
      this.updateTime = setInterval(() => {
        this.currentTime++;
        if (this.currentTime >= Orders.length) this.currentTime = 0;
      }, 3000); //更新
      this.flag = !this.flag;
    },
    stop() {
      clearInterval(this.updateTime);
      this.flag = !this.flag;
    },
    reset() {
      this.currentTime = 0;
    },
  },
  watch: {
    currentTime: function () {
      this.$emit("changeTime", this.currentTime);
      let myDate = new Date("2023-11-30 0:0:0");
      myDate.setMinutes(myDate.getMinutes() + this.currentTime * 15);
      let year = myDate.getFullYear();
      let month = myDate.getMonth() + 1;
      let date = myDate.getDate();
      let hour =
        myDate.getHours() < 10 ? "0" + myDate.getHours() : myDate.getHours();
      let minute =
        myDate.getMinutes() < 10
          ? "0" + myDate.getMinutes()
          : myDate.getMinutes();
      let second =
        myDate.getSeconds() < 10
          ? "0" + myDate.getSeconds()
          : myDate.getSeconds();
      this.nowtime = `${year}/${month}/${date} ${hour}:${minute}`;
    },
    strategy: function() {
      this.$emit("changeStrategy", this.strategy);
    }
  },
  created() {
    // 引入机场节点。注意！可能该节点不在地图内
    const AirlinesJson = require("@/static/json/Airlines_A319");
    this.updateTime = setInterval(() => {
      this.currentTime++;
      if (this.currentTime >= AirlinesJson.length) this.currentTime = 0;
    }, 3000); //更新
  },
};
</script>

<style scoped>
#bar {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  position: relative;
  background: rgba(255, 255, 255, 0.1);
}

.title {
  text-align: center;
  position: relative;
}

.slider {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 100px;
  margin: -50px 0 0 -50px;
}
.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>