<script>
import * as echarts from "echarts";


export default {
  props: ["group_analytic_data"],
  methods:{
    onChange: function(){
      if(this.checked){
        // 比例
        document.getElementById('chart').style.display = 'none';
        document.getElementById('detailContainer').style.display = 'flex';
      }else{
        // 详情
        document.getElementById('detailContainer').style.display = 'none';
        document.getElementById('chart').style.display = 'block';
        // alert('unchecked')
      }
      this.myChart.setOption(this.options);
    }
  },
  watch: {
    group_analytic_data: function () {
      console.log('pie4 detect change')
      console.log(this.group_analytic_data)
      console.log(this.group_analytic_data["team_detail"])
      let team_detail = this.group_analytic_data["team_detail"]
      let teamA = this.group_analytic_data["group_id"]
      let teamB = team_detail["battling_team"][teamA]
      /* this.options.series[0].data = this.group_analytic_data["revenue"].map(function (
        item,
        idx
      ) {
        var item_name;
        if (idx == 0) {
          item_name = "0-100";
        } else if (idx == 1) {
          item_name = "100-1000";
        } else {
          item_name = ">1000";
        }
        return {
          name: item_name,
          value: item,
        };
      }); */
      console.log("total_revenue")
      console.log(team_detail["total_revenue"])
      this.options.series[0].data = [
        { name: "队伍"+ (teamA+1) +"效用", value: team_detail["total_revenue"][teamA] ,itemStyle:{color: '#db4520'} },
        { name: "队伍"+ (teamB+1) + "效用", value: team_detail["total_revenue"][teamB] ,itemStyle: {color: '#8aabca'}},
      ];
      console.log(this.options);
      this.myChart.setOption(this.options);
      
      
      // generate team detail from group_analytic_data["team_detail"]
     

      this.teamA_id = teamA
      this.teamB_id = teamB
      this.teamA_leader = team_detail["header"][teamA]
      this.teamB_leader = team_detail["header"][teamB]
      // keep two digits for float
      this.teamA_age_range = team_detail["age_range"][teamA]
      this.teamB_age_range = team_detail["age_range"][teamB]

      console.log("sex_ratio")
      console.log(teamA)
      console.log(team_detail["sex_ratio"][teamA])
      let tmp =team_detail["sex_ratio"][teamA]

      tmp = tmp.toFixed(2)
      this.teamA_sex_ratio = tmp
      tmp =team_detail["sex_ratio"][teamB]
      tmp = tmp.toFixed(2)
      this.teamB_sex_ratio = tmp
      if (team_detail["in_city"][teamA] == 1){
        this.teamA_in_city = "市内"
    }else{
        this.teamA_in_city = "跨市"
    }
    if (team_detail["in_city"][teamB] == 1){
        this.teamB_in_city = "市内"
    }else{
        this.teamB_in_city = "跨市"
    }
    this.teamA_teammates = team_detail["teammate"][teamA]
    this.teamB_teammates = team_detail["teammate"][teamB]
    },
  },
  mounted() {
    this.myChart = echarts.init(this.$refs.echarts, null, {
      renderer: "canvas",
      useDirtyRect: false,
    });

    this.myChart.setOption(this.options);
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });
  },
  data: function () {
    return {
      // team_deatil
      //teamA
      teamA_id : 0,
      // teamA_name: "队伍A",
      teamA_leader: "队长A",
      teamA_age_range:"18-60",
      teamA_sex_ratio:"1.3",
      teamA_in_city:"市内",
      teamA_teammates:"队员A",

      //teamB
      teamB_id : 1,
      // teamB_name: "队伍B",
      teamB_leader: "队长B",
      teamB_age_range:"18-60",
      teamB_sex_ratio:"2.7",
      teamB_in_city:"市内",
      teamB_teammates:"队员B",


      checked : false,
      
      mycharts: {},
      options: {
        title: {
          text: "组队总效用对比情况",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 22,
          },
          left: "center",
          top: "5px",
        },
        tooltip:{
          trigger: 'item',
          formatter: "{b}: {c} ({d}%)"
        },
        emphasis: {
        itemStyle: {
          shadowBlur: 10,
          shadowOffsetX: 0,
          shadowColor: 'rgba(0, 0, 0, 0.5)'
        }
      },
        series: [
          {
            type: "pie",
            id: "rev_pie",
            emphasis: {
              focus: "self",
            },
            tooltip:{
              show:false,
            },
            data: [{name:"未进行组队",value:0}],
            label:{
          show:true,
          fontStyle: 'bold',
          fontSize: 18,
          position:"inside",
          formatter:"{b}\n{c}"
        },
          },
        ],
      },
      size: {
        width: "100%",
        height: "5.5rem",
      },
      totalsize: {
        width: "100%",
        height: "5.rem",
      },
    };
  },
};
</script>

