<template>
  <div id="pie" :style="size">
    <!-- <el-popover placement="bottom-end" width="300" trigger="click">
      <img src="../../static/img/formula.jpg" style="width: 100%" alt="公式" />
      <el-button
        type="primary"
        icon="el-icon-search"
        size="mini"
        plain
        circle
        slot="reference"
        style="position: absolute; z-index: 9; right: 30px; top: 30px"
      ></el-button>
    </el-popover> -->
    <!-- <el-button icon="el-icon-search" size="mini" type="primary" plain circle @click="open()" style="position:absolute; z-index: 9; left:10px; bottom:10px"></el-button> -->
    <div class="dragRectTop"></div>
    <e-charts :size="size" :options="option" />
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import ECharts from "@/components/ECharts";
export default {
  components: {
    ECharts,
  },
  data: function () {
    return {
      option: {
        title: {
          text: "Contributions of Local Platforms",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 16,
          },
          left: "center",
          top: "5px",
        },
        tooltip: {
          trigger: "item",
          formatter: " {b}:{d}%",
        },
        legend: {
          top: "35px",
          orient: "vertical",
          left: "left",
          textStyle: {
            color: "white", //更改坐标轴文字颜色
            fontSize: 12, //更改坐标轴文字大小
          },
        },
        series: [
          {
            name: "Access From",
            type: "pie",
            radius: ['40%', '70%'],
            left: "left",
            center: ["62%", "60%"], // 这个属性可以调整图像的位置，左面所示为中心
            // roseType: 'area',
            avoidLabelOverlap: true,
            padAngle: 1,
            itemStyle: {
              borderRadius: 8
            },
            label: {
              normal: {
                show: true,
                position: "inner", // 数值显示在内部
                // position: 'center',
                formatter: "{d}%", // 格式化数值百分比输出
                textStyle: {
                  fontSize: 18,
                }
              },
            },
            labelLine: {
              show: false
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
            data: [
              { value: 25, name: "Didi" },
              { value: 20, name: "Caocao" },
              { value: 15, name: "Shouqi" },
              { value: 15, name: "Yangguang" },
              { value: 13, name: "Feima" },
              { value: 12, name: "Zhongjiao" },
            ],
          },
        ],
      },
      size: {
        width: "100%",
        height: "4.5rem",
      },
    };
  },
  props: ["currentTime"],
  watch: {
    // 监听options改变，重新生成图表，动态刷新
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.option);
    },
    currentTime: function () {
      this.option.series[0].data[0].value =
        25 + Math.floor(Math.random() * 8 - 4);
      this.option.series[0].data[1].value =
        20 + Math.floor(Math.random() * 6 - 3);
      this.option.series[0].data[2].value =
        15 + Math.floor(Math.random() * 4 - 2);
      this.option.series[0].data[3].value =
        15 + Math.floor(Math.random() * 4 - 2);
      this.option.series[0].data[4].value =
        13 + Math.floor(Math.random() * 2 - 1);
      this.option.series[0].data[5].value =
        100 -
        this.option.series[0].data[0].value -
        this.option.series[0].data[1].value -
        this.option.series[0].data[2].value -
        this.option.series[0].data[3].value -
        this.option.series[0].data[4].value;
      this.option = { ...this.option };
    },
  },
  mounted() {
    // 首先获取div
    // var mainDiv = document.getElementById('main')
    // 基于准备好的dom，初始化echarts实例
    // this.myChart = echarts.init(this.$refs.echarts);
    // 使用刚指定的配置项和数据显示图表。
    // this.myChart.setOption(this.option);
    let that = this;
  },
  methods: {
    open() {
      const url = require("../../static/img/formula.jpg");
      this.$alert(
        "<img src = " + url + " style=" + "object-fit: contain;" + "/>",
        "Contributions Ratio of Federated Parties计算公式",
        {
          dangerouslyUseHTMLString: true,
        }
      );
    },
  },
};
</script>

<style scoped>
#pie {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}
.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>