<template>
  <div id="pie" :style="size">
    <div class="dragRectTop"></div>
    <e-charts :size="size" :options="option" />
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import ECharts from "@/components/ECharts";
export default {
  components: {
    ECharts,
  },
  data: function () {
    return {
      flag: true,
      size: {
        width: "100%",
        height: "5.5rem",
      },
      option: {
        color: ["#56A3F1", "#FFE434" ,"#67F9D8"],
        title: {
          text: "Algorithm Performance Radar Chart",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 16,
          },
          left: "center",
          top: "5px",
        },
        legend: {
          textStyle: {
            color: "white",
            fontSize: 12,
          },
          top: "32px",
          data: ["Peak", "Average", "Lowest"],
        },
        radar: {
          // shape: "circle",
          center: ["46%", "60%"],
          indicator: [
            { name: "Vehicle", max: 100 },
            { name: "Order", max: 100 },
            { name: "Shortfall", max: 100 },
          ],
          axisName: {
            formatter: "{value}",
            color: "white",
          },
          splitArea: {
            areaStyle: {
              shadowColor: "rgba(0, 0, 0, 0.2)",
              shadowBlur: 10,
            },
          },
          axisLine: {
            lineStyle: {
              color: "rgba(211, 253, 250, 0.8)",
            },
          },
          splitLine: {
            lineStyle: {
              color: "white",
            },
          },
        },
        series: [
          {
            name: "Peak/Average/Lowest",
            type: "radar",
            data: [
              {
                value: [100, 100, 100, 100, 100],
                name: "Peak",
              },
              {
                value: [80, 78, 90, 70, 85],
                name: "Average",
              },
              {
                value: [60, 52, 90, 45, 75],
                name: "Lowest",
              },
            ],
          },
        ],
      },
    };
  },
  mounted() {},
  methods: {},
};
</script>

<style scoped>
#pie {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>