<template>
  <div id="pie" :style="totalsize">
    <div class="dragRectTop"></div>
    <div ref="echarts" :style="size">
      <!-- <e-charts :size="size" :options="options" /> -->
    </div>
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  props: ["group_driver_pie_data"],
  watch: {
    group_driver_pie_data: function () {
      this.options.series[0].data = this.group_driver_pie_data;
      console.log(this.options);
      this.myChart.setOption(this.options);
    },
  },
  mounted() {
    this.myChart = echarts.init(this.$refs.echarts, null, {
      renderer: "canvas",
      useDirtyRect: false,
    });
    window.addEventListener("resize", () => {
      this.myChart.resize();
    });

    this.myChart.setOption(this.options);
  },
  data: function () {
    return {
      mycharts: {},
      options: {
        title: {
          text: "组队规模对比",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 22,
          },
          left: "center",
          top: "5px",
        },
        /* legend: {
          left: "left",
          bottom: '0',
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 22,
          },
        }, */
        tooltip: {
          show: false,
          trigger: "item",
          formatter: "{b}: {c} ({d}%)",
        },

        series: [
          {
            type: "pie",
            id: "driver_pie",
            top: "10%",
            emphasis: {
              focus: "self",
            },
            label: {
              show: true,
              position: "inside",
              formatter: "{b}\n{c}人 \n {d}\%",
              fontStyle: "bold",
              shadowColor: "black",
              fontSize: 18,
              // rotate:true
            },
            data: [],
          },
        ],
        emphasis: {
          itemStyle: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: "rgba(0, 0, 0, 0.5)",
          },
        },
      },
      size: {
        width: "100%",
        height: "5.5rem",
      },
      totalsize: {
        width: "100%",
        height: "5.rem",
      },
    };
  },
};
</script>

<style scoped>
#pie {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.title {
  text-align: center;
  position: relative;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>
