<template>
  <div id="orderbar" :style="size">
    <div class="dragRectTop"></div>
    <e-charts :size="size" :options="option" />
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import ECharts from "@/components/ECharts";
import * as echarts from "echarts";
var data1 = [];
var data2 = [];

export default {
  components: {
    ECharts,
  },
  data: function () {
    return {
      flag: true,
      data1: [],
      data2: [],
      size: {
        width: "100%",
        height: "6.3rem",
      },
      option: {
        title: {
          text: "Total Number of Orders and Drivers",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 16,
          },
          left: "center",
          top: "5px",
        },
        legend: {
          show: true,
          top: "390px",
          align: "auto",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 16,
          },
        },
        tooltip: {
          triggerOn: "none",
          position: function (pt) {
            return [pt[0], 150];
          },
        },
        xAxis: {
          show: false,
          type: "value",
          axisPointer: {
            value: 3,
            snap: true,
            lineStyle: {
              color: "#7581BD",
              width: 2,
            },
            label: {
              show: false,
              formatter: function (params) {
                return String(params.value);
              },
              precision: "auto",
              backgroundColor: "#7581BD",
            },
            handle: {
              show: true,
              size: 10,
              margin: 12,
              color: "#7581BD",
            },
          },
          splitLine: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          axisTick: {
            inside: true,
          },
          splitLine: {
            show: false,
          },
          axisLabel: {
            inside: true,
            formatter: "{value}\n",
          },
          z: 10,
        },
        grid: {
          top: 80,
          left: 15,
          right: 15,
          height: 280,
        },
        dataZoom: [
          {
            type: "inside",
            throttle: 50,
          },
        ],
        series: [
          {
            name: "Drivers",
            type: "line",
            smooth: true,
            symbol: "circle",
            symbolSize: 5,
            sampling: "average",
            itemStyle: {
              color: "#0770FF",
            },
            legend:{
              textStyle:18,
            },
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgba(58,77,233,0.8)",
                },
                {
                  offset: 1,
                  color: "rgba(58,77,233,0.3)",
                },
              ]),
            },

            data: data1,
          },
          {
            name: "Orders",
            type: "line",
            smooth: true,
            symbol: "circle",
            symbolSize: 5,
            sampling: "average",
            itemStyle: {
              color: "#F2597F",
            },
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgba(213,72,120,0.8)",
                },
                {
                  offset: 1,
                  color: "rgba(213,72,120,0.3)",
                },
              ]),
            },
            data: data2,
          },
        ],
      },
    };
  },
  props: ["currentTime"],
  watch: {
    // 监听options改变，重新生成图表，动态刷新
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.option);
    },
    currentTime: function () {
      var ordersource = require("@/static/json/predictMap.json")[
        parseInt(this.currentTime)
      ];
      var driversource = require("@/static/json/predictDriverMap.json")[
        parseInt(this.currentTime)
      ];

      var orders = 0;
      var drivers = 0;

      driversource.forEach((item) => {
        drivers += item[2];
      });

      ordersource.forEach((item) => {
        orders += item[2];
      });

      this.data1.push([0, drivers]);
      this.data2.push([0, orders]);

      this.data1 = this.data1.slice(-18);
      this.data2 = this.data2.slice(-18);

      for (var i = 0; i < this.data1.length; i++) {
        this.data1[i][0] = i + 1;
        this.data2[i][0] = i + 1;
      }

      console.log(this.data1);
      console.log(this.data2);

      this.option.series[0].data = this.data1;
      this.option.series[1].data = this.data2;
      this.option = { ...this.option };
    },
  },
  mounted() {
    // 首先获取div
    // var mainDiv = document.getElementById('main')
    // 基于准备好的dom，初始化echarts实例
    // this.myChart = echarts.init(this.$refs.echarts);
    // 使用刚指定的配置项和数据显示图表。
    // this.myChart.setOption(this.option);
    let that = this;
  },
  methods: {},
};
</script>

<style scoped>
#orderbar {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>
