<template>
  <div id="pie">
    <div class="dragRectTop"></div>
      <div id="graph" ref="echarts" :style="size"></div>
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  props: ["currentTime", "strategy"],
  data: function () {
    return {
      options: {
        color: ["#56A3F1", "#67F9D8", "#FFE434"],
        title: {
          text: "Response Rate in 5min",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 16,
          },
          left: "center",
          top: "5px",
        },
        tooltip: {
          trigger: "item",
          formatter: " {b}:{d}%",
        },
        legend: {
          top: "35px",
          orient: "vertical",
          left: "left",
          textStyle: {
            color: "white", //更改坐标轴文字颜色
            fontSize: 14, //更改坐标轴文字大小
          },
        },
        series: [
          {
            name: "Access From",
            type: "pie",
            radius: "70%",
            left: "left",
            center: ["50%", "60%"], // 这个属性可以调整图像的位置，左面所示为中心
            // roseType: "radius",
            avoidLabelOverlap: true,

            label: {
              normal: {
                show: true,
                fontSize: 18,
                position: "inner", // 数值显示在内部
                formatter: "{d}%", // 格式化数值百分比输出
              },
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
            data: [
              { value: 67, name: "Response Rate" },
              { value: 8, name: "Rejection Rate" },
              { value: 25, name: "Waiting Rate" },
            ],
          },
        ],
      },
      size: {
        width: "100%",
        height: "5.5rem",
      },
    };
  },
  watch: {
    // 监听options改变，重新生成图表，动态刷新
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.options);
    },
    currentTime: function () {
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        this.curr_orders = OrdersGreedy[this.currentTime]['responds'];
      } else {
        this.curr_orders = Orders[this.currentTime]['responds'];
      }
      this.options.series[0].data = this.curr_orders;
      this.options = { ...this.options };
    },
    strategy: function () {
      console.log("orderbar capture strategy");
      console.log(this.strategy);
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        this.curr_orders = OrdersGreedy[this.currentTime]['responds'];
      } else {
        this.curr_orders = Orders[this.currentTime]['responds'];
      }
      this.options.series[0].data = this.curr_orders;
      this.options = { ...this.options };
    },
  },
  mounted() {
    let that = this;
    that.myChart = that.$echarts.init(that.$refs.echarts);
    that.myChart.setOption(that.options);
  },
  methods: {},
};
</script>

<style scoped>
#pie {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>
