<template>
  <div id="pie" :style="size">
    <div class="dragRectTop"></div>
    <div id="graph" ref="echarts" :style="size"></div>
    <!-- <e-charts v-else :size="size" :options="options2" /> -->
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  props: ["currentTime", "strategy"],
  data: function () {
    return {
      flag:true,
      options: {
        title: {
          text: 'Passenger Trip Distance Analysis',
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 16,
          },
          left: "center",
          top: "5px"
        },
        tooltip: {
          trigger: 'item',
          formatter: " {b}内:{c}({d}%)"
        },
        // legend: {
        //   // top: '90%',
        //   left: 'right',
        //   orient: 'vertical',
        //   textStyle: {
        //     color: "rgba(255,255,255,0.6)",
        //     fontSize: "12"
        //   }
        // },
        series: [
          {
            name: 'Access From',
            type: 'pie',
            radius: '75%',
            left: 'left',
            center: ["50%", "55%"], // 这个属性可以调整图像的位置，左面所示为中心
            // roseType: "radius",
            // avoidLabelOverlap: false,
            color: ['#FFA500', '#FF8c00', '#FF7F50', '#FF6347', '#FF4500'],
            label: {
              // show: false,
              position: 'inside',
              overflow: 'break',
              textStyle: {
                color: 'white',  //更改坐标轴文字颜色
                fontSize : 18      //更改坐标轴文字大小
              }
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            },
            data: []
          }
        ]
      },
      size: {
        width: '100%',
        height: '5rem'
      },
    }
  },
  watch: {
    // 监听options改变，重新生成图表，动态刷新
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.options);
    },
    currentTime: function () {
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        this.options.series[0].data = OrdersGreedy[this.currentTime]["distance"];
      } else {
        this.options.series[0].data = Orders[this.currentTime]["distance"];
      }

      this.options = { ...this.options };
    },
    strategy: function() {
      console.log("pies capture strategy");
      console.log(this.strategy);
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        this.options.series[0].data = OrdersGreedy[this.currentTime]["distance"];
      } else {
        this.options.series[0].data = Orders[this.currentTime]["distance"];
      }

      this.options = { ...this.options };
    }
  },
  mounted () {
    let that = this;
    that.myChart = that.$echarts.init(that.$refs.echarts);
    that.myChart.setOption(that.options);
  },
  methods:{
    qiehuan(){
      this.flag = !this.flag
    }
  }
}
</script>

<style scoped>

#pie{
	/* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255,255,255,0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.dragRectTop{
    width: 100%;
    height: 20px;
    position: absolute;
    top: 0;
    left: 0
}

.dragRectTop::before{
    content: "";
    width: 20px;
    height: 20px;
    border-left: 2px solid #008c14;
    border-top: 2px solid #008c14;
    display: block;
    position: absolute;
    left: 0;
    top: 0;
}

.dragRectTop::after{
    content: "";
    width: 20px;
    height: 20px;
    border-right: 2px solid #008c14;
    border-top: 2px solid #008c14;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
}

.dragRectBottom{
    width: 100%;
    height: 20px;
    position: absolute;
    bottom: 0;
    left: 0
}

.dragRectBottom::before{
    content: "";
    width: 20px;
    height: 20px;
    border-left: 2px solid #008c14;
    border-bottom: 2px solid #008c14;
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
}

.dragRectBottom::after{
    content: "";
    width: 20px;
    height: 20px;
    border-right: 2px solid #008c14;
    border-bottom: 2px solid #008c14;
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
}
</style>