<template>
  <div id="line" :style="size">
    <div class="dragRectTop"></div>
    <div id="lines" ref="echarts" :style="size"></div>
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";


export default {
  props: ["currentTime", "strategy"],
  data: function () {
    return {
      size: {
        width: "100%",
        height: "3rem",
      },
      options: {
        title: {
          text: "Utility of Platform Broker",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 16,
          },
          left: "center",
          top: "5px",
        },
        legend: {
          selectedMode: true, //可点击
          data: ["Utility/million", "Interval Utility/million"],
          bottom:"2",
          textStyle: {
            color: "white",
            fontSize: 12,
          },
        },
        grid: {
          bottom: 30,
          right: 30,
          left: 40,
        },
        xAxis: [
          {
            show: false,
            type: "category",
            splitLine: {
              //不显示分割线
              show: false,
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            name: "Utility/million",
            nameTextStyle: {
              padding: [0, 0, 0, 0] // 上右下左与原位置距离
            },
            splitLine: {
              //显示分割线
              show: true,
            },
          },
          {
            type: "value",
            name: "Interval Utility/million",
            nameTextStyle: {
              padding: [0, 70, 0, 0] // 上右下左与原位置距离
            },
            splitLine: {
              show: true,
            },
          },
        ],
        series: [
          {
            name: "Utility/million",
            type: "bar",
            data: [],
            itemStyle: {
              color: "rgba(132, 136, 211, 1)",
            },
          },
          {
            name: "Interval Utility/million",
            type: "line",
            yAxisIndex: 1, //索引从0开始
            data: [],
            itemStyle: {
              color: "rgba(91, 245, 204, 1)",
            },
          },
        ],
      },
    };
  },
  watch: {
    // 监听options改变，重新生成图表，动态刷新
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.options);
    },
    currentTime: function () {
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        this.curr_orders = OrdersGreedy[this.currentTime]["earn"];
      } else {
        this.curr_orders = Orders[this.currentTime]["earn"];
      }
      this.options.series[0].data = this.curr_orders[0];
      this.options.series[1].data = this.curr_orders[1];
      this.options = { ...this.options };
    },
    strategy: function () {
      console.log("lines capture strategy");
      console.log(this.strategy);
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        this.curr_orders = OrdersGreedy[this.currentTime]["earn"];
      } else {
        this.curr_orders = Orders[this.currentTime]["earn"];
      }
      this.options.series[0].data = this.curr_orders[0];
      this.options.series[1].data = this.curr_orders[1];
      this.options = { ...this.options };
    },
  },
  mounted() {
    let that = this;
    that.myChart = that.$echarts.init(that.$refs.echarts);
    that.myChart.setOption(that.options);
  },
  methods: {},
};
</script>

<style scoped>
#line {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>
