<template>
  <div id="bar">
    <div class="dragRectTop"></div>
    <h2 style="text-align:center;">Demonstration Controller</h2>
    <!-- <div>{{this.curr_info}}</div> -->
    <div style="font-size:18px;font-weight:bold;">队伍基本信息：
            <!-- 使用容器包裹三个文本 -->
      <div class="container">
        <span class="text">队伍编号：
        {{ this.curr_info.group_id}}</span>
        <span class="text">队长编号：
        {{ this.curr_info.header}}</span>
        <span class="text">队伍规模：
          <span v-if="this.curr_info.nodes_num != undefined">{{ this.curr_info.nodes_num +8}}</span>
        </span>
      </div>
      
      <div style="font-size:14px;font-weight:bold;">总效用/周:
        <div class="text" style="text-align:center;font-size:16px;">
        {{ this.curr_info.sum_of_total_revenue}}</div>
      </div>
    </div>
    <div class="container" style="font-size:14px;">
      队伍数量：
    <div class="select-box" style="margin-top: 6px;">
      
    <select v-model="group_level" style="font-size: 16px; font-weight:bold;" >
      <option disabled value="">请选择</option>
      <option>0</option>
      <option>6</option>
      <option>10</option>
      <option>30</option>
      <option>50</option>
    </select>
    </div>
  </div>
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>

import ECharts from '@/components/ECharts'

var Time = 0;
export default {
  components: {
    ECharts
  },
  props:['curr_info'],
  data: function () {
    return {
      currentTime:0,
      updateTime:null,
      nowtime:"",
      flag: false,
      group_level: 0,
    }
  },
  mounted(){
    this.curr_info.group_id = 0;
    this.curr_info.header = 0;
    this.curr_info.nodes_num = 100;
    this.curr_info.sum_of_total_revenue = 100*200 + Math.floor(Math.random()*1000);
  },
  methods:{
    changeGroupLevel() {
      this.$emit("group_level_change", this.group_level);
    }
  },
  watch: {
    group_level: function() {
      this.$emit("group_level_change", this.group_level);
    }
  },
}
</script>

<style scoped>
#bar{
	/* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255,255,255,0.2);
  position: relative;
  background: rgba(255, 255, 255, 0.1);
}

/* 定义一个容器，使用flex布局 */
.container {
  display: flex;
  justify-content: space-between; /* 使子元素沿主轴均匀分布 */
  align-items: center; /* 使子元素沿交叉轴居中对齐 */
  margin-top: 6px;
  margin-bottom: 6px;
}

/* 定义三个文本的样式 */
.text {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.9);
}

/* 定义下拉框的样式 */
.select-box {
  position: relative;
  width: 100px;
  height: 36px;
  border: none;
  border-radius: 10px;
  background-color: #fff;
  color: #333;
  font-size: 16px;
}

/* 定义下拉框的箭头 */
.select-box::after {
  content: "▼";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

/* 定义下拉框的选项 */
.select-box select {
  appearance: none;
  outline: none;
  border: none;
  width: 100%;
  height: 100%;
  padding: 10px;
  background-color: transparent;
  color: inherit;
}



/* 定义下拉框选项的悬停效果 */
.select-box select option:hover {
  filter: brightness(80%); /* 调暗颜色 */
}

.title {
  text-align: center;
  position: relative;
}
.slider{
  position: absolute;
  top: 50%;
  left: 50%;
  height: 100px;
  margin:-50px 0 0 -50px  
}
.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>