<template>
  <div id="pie" :style="size">
    <!-- <el-popover
      placement="bottom-end"
      width="300"
      title="运力差=需求量-供给量"
      trigger="click"
    >
      <el-button
        type="primary"
        icon="el-icon-search"
        size="mini"
        plain
        circle
        slot="reference"
        style="position: absolute; z-index: 9; right: 30px; top: 5px"
      ></el-button>
    </el-popover> -->
    <div class="dragRectTop"></div>
    <e-charts :size="size" :options="option" />
    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import ECharts from "@/components/ECharts";
export default {
  components: {
    ECharts,
  },
  data: function () {
    return {
      size: {
        width: "100%",
        height: "3rem",
      },
      option: {
        color: [ "#5eb3ff","#f2597f","#ffe434"],
        title: {
          text: "Capacity Shortfall",
          textStyle: {
            color: "rgba(255,255,255,0.9)",
            fontSize: 16,
          },
          left: "center",
          top: "5px",
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        legend: {
          data: ["Drivers", "Orders", "Shortfall"],
          top: "40px",
          textStyle: {
            color: "white", //更改坐标轴文字颜色
            fontSize: 14, //更改坐标轴文字大小
          },
        },
        grid: {
          top: "80px",
          bottom: "10px",
          containLabel: true,
        },
        xAxis: [
          {
            type: "value",
          },
        ],
        yAxis: [
          {
            show: false,
            type: "category",
            axisTick: {
              show: false,
            },
            data: ["Data Details"],
          },
        ],
        series: [
          {
            name: "Drivers",
            type: "bar",
            label: {
              show: true,
              position: "inside",
              textStyle: {
                fontSize: 18,
              }
            },
            emphasis: {
              focus: "series",
            },
            data: [3620],
          },
          {
            name: "Orders",
            type: "bar",
            stack: "Total",
            label: {
              show: true,
              textStyle: {
                fontSize: 18,
              }
            },
            emphasis: {
              focus: "series",
            },
            data:[5320],
          },
          {
            name: "Shortfall",
            type: "bar",
            stack: "Total",
            label: {
              show: true,
              position: "inside",
              textStyle: {
                fontSize: 18,
              }
            },
            emphasis: {
              focus: "series",
            },
            data: [-1700],
          },
        ],
      },
    };
  },
    props: ["currentTime"],
    watch: {
    // 监听options改变，重新生成图表，动态刷新
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.option);
    },
    currentTime: function () {
      this.option.series[0].data = [this.option.series[0].data[0] + Math.floor((Math.random()*100) - 50)];
      this.option.series[1].data = [this.option.series[1].data[0] + Math.floor((Math.random()*100) - 50)];
      this.option.series[2].data = [this.option.series[0].data - this.option.series[1].data[0]];
      // console.log( this.option.series[0].data);
      this.option = { ...this.option};
    },
  },
  mounted() {
    // 首先获取div
    // var mainDiv = document.getElementById('main')
    // 基于准备好的dom，初始化echarts实例
    // this.myChart = echarts.init(this.$refs.echarts);
    // 使用刚指定的配置项和数据显示图表。
    // this.myChart.setOption(this.option);
    let that = this;
  },
  methods: {},
};
</script>

<style scoped>
#pie {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>