<template>
  <div id="ver-bar" :style="size">
    <div class="dragRectTop"></div>
    <e-charts :size="size" :options="options" />
    <div class="dragRectBottom"></div>
  </div>
</template>


<script>
import ECharts from "@/components/ECharts";
var data = []; //完成百分比
var titlename = []; //始终点
export default {
  components: {
    ECharts,
  },
  data: function () {
    return {
      Barcolor: [],
      options: {
        year: 2019,
        title: {
          text: "Current Order Completion Status",
          left: "center",
          top: "5px",
          textStyle: {
            color: "#fff",
            fontSize: 16,
          },
        },
        legend: {
          show: false,
        },
        grid: {
          top: "15%",
          left: "-5%",
          right: "5%",
          bottom: "5%",
          containLabel: true,
        },

        yAxis: {
          show: false,
          max: 100, //设置最大值
        },
        xAxis: [
          {
            max: 4.5,
            show: true,
            data: titlename,
            inverse: true,
            axisLine: {
              show: false,
            },
            splitLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              color: "#ffffff",
              fontSize: "12",
            },
          },
        ],
        series: [
          {
            realtimeSort: true, //柱状图自动排序，排序自动让Y轴名字跟着数据动
            showBackground: true,
            backgroundStyle: {
              color: "rgba(220, 220, 220, 0.8)",
              borderRadius: 0, // 统一设置四个角的圆角大小
            },
            name: "条",
            type: "bar",
            data: data,
            barGategoryGap: 10,
            barWidth: 35,
            itemStyle: {
              normal: {
                barBorderRadius: 0,
              },
            },
            label: {
              normal: {
                show: true,
                valueAnimation: true,
                position: "inside",
                formatter: "{c}%",
                // color: "#000000", //进度条百分比颜色
                textStyle: {
                  color: "black",
                  fontSize: 10,
                },
              },
            },
          },
        ],
      },
      size: {
        width: "100%",
        height: "4.5rem",
      },
    };
  },
  props: ["currentTime", "strategy"],
  watch: {
    currentTime: function () {
      var testJson = [];
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        testJson = OrdersGreedy;
      } else {
        testJson = Orders;
      }

      var _curr_orders = testJson[this.currentTime]["orders"].map(function (dataItem) {
        return dataItem.title;
      });

      var _curr_progresses = testJson[this.currentTime]["orders"].map(function (
        dataItem
      ) {
        return {
          value: ((1 - dataItem.value / dataItem.full_value) * 100).toFixed(2),
          itemStyle: {
            color: dataItem.color,
          },
        };
      }).slice(0, 6); // 只取前6个

      this.options = { ...this.options };

      this.options.xAxis[0].data = _curr_orders;
      this.options.series[0].data = _curr_progresses
        // .sort(function (a, b) {
        //   return b.value - a.value;
        // })
        .slice(0, 10);
      this.options = { ...this.options };
    },
    strategy: function () {
      console.log("progress capture strategy");
      console.log(this.strategy);
      var testJson = [];
      const Orders = require("@/static/json/Orders");
      const OrdersGreedy = require("@/static/json/OrdersGreedy");
      if (this.strategy == 0) {
        testJson = OrdersGreedy;
      } else {
        testJson = Orders;
      }

      var _curr_orders = testJson[this.currentTime]["orders"].map(function (dataItem) {
        return dataItem.title;
      });

      var _curr_progresses = testJson[this.currentTime]["orders"].map(function (
        dataItem
      ) {
        return {
          value: ((1 - dataItem.value / dataItem.full_value) * 100).toFixed(2),
          itemStyle: {
            color: dataItem.color,
          },
        };
      });

      this.options = { ...this.options };

      this.options.xAxis[0].data = _curr_orders;
      this.options.series[0].data = _curr_progresses
        .sort(function (a, b) {
          return b.value - a.value;
        })
        .slice(0, 10);
      this.options = { ...this.options };
    },
  },
};
</script>

<style scoped>
#ver-bar {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
}

.title {
  text-align: center;
  position: relative;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>
