<template>
  <div id="bar">
    <div class="dragRectTop"></div>
    <h2 style="text-align: center; margin-top: 5px">Demonstration Controller</h2>
    <div class="block" style="margin: auto; margin-top: 5%; width: 80%">
      <div class="demonstration" style="font-size: 16px">
        Time:
        {{ nowtime }}
      </div>
      <div>
        <el-slider v-model="currentTime" :max="96" :show-tooltip="false" ></el-slider>
      </div>
    </div>

    <div style="text-align: center; margin-bottom: 5%;">
      <el-select v-model="value" placeholder="Heatmap Selection" style="
      width: 170px;
      height: 40px;
      margin-right: 10px;
      vertical-align: middle;
      display: inline-block;">
        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
      <el-button v-if="this.flag === true" type="success" @click="start"
        style="font-size: 25px; height: 40px; width: 40px; padding: 0; vertical-align: middle; display: inline-flex; align-items: center; justify-content: center;"
        icon="el-icon-video-play" circle>
      </el-button>
      <el-button v-if="this.flag === false" type="warning" @click="stop"
        style="font-size: 25px; height: 40px; width: 40px; padding: 0; vertical-align: middle; display: inline-flex; align-items: center; justify-content: center;"
        icon="el-icon-video-pause" circle>
      </el-button>
      <el-button type="primary" @click="reset"
        style="font-size: 25px; height: 40px; width: 40px; padding: 0; vertical-align: middle; display: inline-flex; align-items: center; justify-content: center;"
        icon="el-icon-refresh" circle>
      </el-button>
    </div>


    <div class="dragRectBottom"></div>
  </div>
</template>

<script>
import ECharts from "@/components/ECharts";

var Time = 0;
export default {
  components: {
    ECharts,
  },
  data: function () {
    return {
      options: [
        {
          value: "1",
          label: "No Incentives",
        },
        {
          value: "2",
          label: "Fixed-Rule Pricing Algorithm",
        },
        {
          value: "3",
          label: "Dynamic Prcing Algorithm",
        },
      ],
      value: "1",
      currentTime: 20,
      updateTime: null,
      nowtime: "Loading……",
      flag: false,
    };
  },
  methods: {
    changeValue() {
      this.$emit("value_change", this.value);
    },
    start() {
      const AirlinesJson = require("@/static/json/Airlines_A319");
      this.updateTime = setInterval(() => {
        this.currentTime++;
        if (this.currentTime >= AirlinesJson.length) this.currentTime = 0;
      }, 3000); //更新
      this.flag = !this.flag;
    },
    stop() {
      clearInterval(this.updateTime);
      this.flag = !this.flag;
    },
    reset() {
      this.currentTime = 0;
    },
  },
  watch: {
    value: function () {
      this.$emit("value_change", this.value);
    },
    currentTime: function () {
      this.$emit("changeTime", this.currentTime);
      let myDate = new Date("2023-11-30 0:0:0");
      myDate.setMinutes(myDate.getMinutes() + this.currentTime * 15);
      let year = myDate.getFullYear();
      let month = myDate.getMonth() + 1;
      let date = myDate.getDate();
      let hour =
        myDate.getHours() < 10 ? "0" + myDate.getHours() : myDate.getHours();
      let minute =
        myDate.getMinutes() < 10
          ? "0" + myDate.getMinutes()
          : myDate.getMinutes();
      let second =
        myDate.getSeconds() < 10
          ? "0" + myDate.getSeconds()
          : myDate.getSeconds();
      this.nowtime = `${year}/${month}/${date} ${hour}:${minute}`;
    },
  },
  created() {
    // 引入机场节点。注意！可能该节点不在地图内
    const AirlinesJson = require("@/static/json/Airlines_A319");
    this.updateTime = setInterval(() => {
      this.currentTime++;
      if (this.currentTime >= AirlinesJson.length) this.currentTime = 0;
    }, 3000); //更新
  },
};
</script>

<style scoped>
#bar {
  /* background-image: url(../assets/line.png); */
  border: 0.5px solid rgba(255, 255, 255, 0.2);
  position: relative;
  background: rgba(255, 255, 255, 0.1);
}

.title {
  text-align: center;
  position: relative;
}

.slider {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 100px;
  margin: -50px 0 0 -50px;
}

.dragRectTop {
  width: 100%;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.header h2 {
  width: 100%;
  font-size: 0.4rem;
  line-height: 1.25rem;
}

.dragRectTop::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.dragRectTop::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-top: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
}

.dragRectBottom {
  width: 100%;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.dragRectBottom::before {
  content: "";
  width: 20px;
  height: 20px;
  border-left: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
}

.dragRectBottom::after {
  content: "";
  width: 20px;
  height: 20px;
  border-right: 2px solid #008c14;
  border-bottom: 2px solid #008c14;
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>